<template lang="html">
    <div class="songCard" @click="onClickCard">
        <div class="img-wrap">
            <img v-lazy="$utils.getImgUrl(img, 200)">
            <div class="desc-wrap" v-if="desc">
                <span class="desc">{{ desc }}</span>
            </div>
            <miniIcon class="play-icon text-red" type="zanting4"></miniIcon>
        </div>
        <p class="fs-sm text-dark-1 text-ellipsis">{{ name }}</p>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        props: ['id', 'img', 'name', 'desc'],
        data: function () {
            return {}
        },
        methods: {
            onClickCard(){
                this.$router.push(`/home/suggestList/${this.id}`)
            }
        }
    }
</script>

<style scoped lang="scss">
.songCard{
    position: relative;
    width: calc(20% - 8px);
    margin: 4px;
    margin-bottom: 32px;
    cursor: pointer;
    .img-wrap{
        position: relative;
        width: 100%;
        padding-top: 100%;
        margin-bottom: 8px;
        border-radius: 4px;
        overflow: hidden;
        img{
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
        .desc-wrap{
            position: absolute;
            padding: 6px;
            left: 0;
            right: 0;
            top: 0;
            background-color: rgba(0,0,0,0.4);
            transform: translateY(-100%);
            transition: all 0.3s;
            .desc {
                color: white;
                font-size: 12px;
            }
        }
        .play-icon {
            opacity: 0;
            position: absolute;
            right: 4px;
            bottom: 4px;
            font-size: 24px;
            transition: all 0.3s;
            color: white;
        }

        &:hover {
            .desc-wrap {
                transform: translateY(0);
            }

            .play-icon {
                opacity: 1;
            }
        }
    }

}

</style>