<script>
    export default {
        name: 'MiniIcon',
        props: {
            type: { type: String, default: ''},
            size: {type: Number, default: 24}
        },
        data: function () {
            return {

            }
        },
        methods: {
            getIconCls() {
                return `icon-${this.type}`
            },
            getIconStyle() {
                return { fontSize: this.size + 'px'}
            },
            onClick(e) {
                this.$emit('click', e)
            }
        },
        render(){
            const Icon = (
                    <i onClick={this.onClick}
                       class={`iconfont ${this.getIconCls()}`}
                       style={this.getIconStyle()}
                    />
            )
            return Icon
        }

    }
</script>

<style scoped lang="scss">
    span{
        display: block;
        width: 80px;
        height: 48px;
        background-color: white;
    }
</style>