import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Suggest from "../views/Suggest.vue";
import Found from "../views/Found.vue";
import suggestList from "../views/SuggestList/SuggestList.vue";
import MvDetail from "../views/MvDetail.vue";
import Search from "../views/search/SearchList.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    children: [
      {path: '/home', name: 'found', component: Found},
      {path: '/suggest', name: 'suggest', component: Suggest},
      {path: '/home/suggestList/:id', name: 'suggestList', component: suggestList, props: true},
      {path: '/mv/:id', name: 'mv', component: MvDetail, props: (route) =>  ({id: Number(route.params.id)})},
      {path: '/search/:keyword', name: 'search', component: Search, props: (route) =>  ({keyword: route.params.keyword}),
        children: [
          // {path: '/',redirect: 'songs'},
          // {path: 'songs', name: 'searchSongs', component: SearchSong}
        ]
      }
    ]
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

export default router;
