<template>
  <div class="p-5">
    <SHeader :img="playList.coverImgUrl" :title="playList.name"
             :avatar="playList.creator.backgroundUrl" v-if="playList.creator" :time="playList.createTime"
             :createdMan="playList.creator.nickname" :desc="playList.description"
              :tag="playList.tags"></SHeader>
    <!--end of Header-->
    <div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="歌曲列表" name="first">
          <SongList :songList="songs"></SongList>
        </el-tab-pane>
        <el-tab-pane label="评论" name="second">
          <Comment :id="id" type="playList"></Comment>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { getListDetail, getSongDetail } from "../../api/suggestList"
  import SHeader from './Header.vue'
  import SongList from '../../base/SongList.vue'
  import Comment from '../../base/Comment.vue'
  const MAX = 500
  export default {
    data: function () {
      return {
        playList: {},
        detList: {},
        activeName: 'first',
        songs: []
      }
    },
    created(){
      this.getListDetail()
//      this.getSongList(this.playList)

    },

    methods: {
      async getListDetail(){
        const { playlist } = await getListDetail({id: this.id})
        this.playList = playlist
        this.getSongList(playlist)
      },
      async getSongList(playList){
        const trackIds = playList.trackIds.map(({id})=> id)
        const songDetails = await getSongDetail(trackIds.slice(0,MAX))
        const songs = songDetails.songs.map(({id, name, al, ar, mv, dt}) =>
          this.$utils.createSong({id,name,artists: ar, duration: dt, mvid: mv, albumName: al.name, img: al.picUrl}))
        this.songs = songs
        console.log(this.songs)
      },
      handleClick(){

      }
    },
    computed:{
      id(){
        return Number(this.$route.params.id)
      }
    },
    components: {SHeader,SongList,Comment}
  }

</script>

<style lang="scss">
  .el-tabs__item{
    color: #5C5C5C !important;
  }
  .el-tabs__item:hover{
    color: #b1b1b1 !important;
  }
  .el-tabs__item.is-active {
    color: #f44336 !important;
  }
  .el-tabs__active-bar{
    background-color: #f44336 !important;
  }
  .el-tabs__header{
    margin: 0 0 3px;
  }
</style>
