<template>
    <div class="switch" @click="openIcon">
        <i></i>

    </div>
</template>

<script>
    export default {
        props: {

        },
        data: function () {
            return {
                open: true
            }
        },
        methods: {
            openIcon(){
                let $switch = document.querySelector('.switch i');
                if(this.open){
                    $switch.style.transform = 'translate(25px,-2px)'
                    $switch.style.background = '#f99f98'
                }else{
                    $switch.style.transform = 'translate(0px,-2px)'
                    $switch.style.background = 'white'
                }
                this.open = !this.open
            }
        },

    }
</script>

<style scoped lang="scss">
    .switch{
        width: 50px;
        height: 20px;
        border-radius: 20px;
        background: #ededed;
    i{
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background: white;
        transition: .5s ease-out;
        display: inline-block;
        transform: translateY(-2px);
        box-shadow: 0 0 1px 0px #b1b1b1;
    }

    }

</style>