<template>
    <div class="out-volume" :style="`width: ${this.width}`" ref="outVol" @click="changeVolume($event)">
        <div class="in-volume" ref="inVol">
            <div>
                <div class="btn-volume" ref="btn" :class="showBtn?'hidden':'block'"></div>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        props: ["disabled","width","showBtn","percent"],
        data: function () {
            return {

            }
        },
        mounted(){
            if(!this.disabled){
                this.$refs.inVol.style.transform = "translateX(0px)"
            }
        },
        methods: {
            changeVolume(e){
                if(this.disabled) {
                    let n = e.offsetX
                    let rect = this.$refs.outVol.getBoundingClientRect()
                    const offsetWidth = Math.max(
                            0,
                            Math.min(Math.floor(e.pageX - rect.left), n)
                    )
                    let inWidth = this.$refs.inVol.clientWidth
                    let percent = (offsetWidth/inWidth).toFixed(2)
                    this.$refs.inVol.style.transform = `translateX(${offsetWidth}px)`
                    this.$emit('changeVolume', percent)
                    this.$emit('changeProgress', percent)
                }
            },
            updateProgress(percent){
                if(this.percent){
                    let inWidth = this.$refs.inVol.clientWidth
                    this.$refs.inVol.style.transform = `translateX(${percent*inWidth}px)`
                }
            }

        },
        watch: {
            percent(percent){
                this.updateProgress(percent)
            }
        }

    }
</script>

<style lang="scss" scoped>
    .hidden{
        display: none;
    }
    .block{
        display: block;
    }
    .out-volume{
        height: 12px;
        background: transparent;
        position: relative;
        cursor: pointer;
        overflow: hidden;
    .in-volume{
        position: absolute;
        background-color: #f44336;
        width: 100%;
        left: -100%;
        margin-top: 4px;
        transform: translateX(50%);
        height: 2px;

    .btn-volume{
        position: absolute;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        top: -4px;
        right: 0px;
        z-index: 999;
        background-color: #f44336;
    }
    }


    }

</style>