<template lang="html">
    <div class="songCard" @click="onClickCard" :class="this.flex?'d-flex ai-center':''"
         :style="`width: ${this.width}%;}`">
        <div class="img-wrap" :style="`padding-top: ${this.padtop}`">
            <img v-lazy="$utils.getImgUrl(img)">
            <div class="viewCount">
                <miniIcon type="sanjiao--copy" :size=12></miniIcon>
                <span class="pl-1">{{$utils.formatCount(viewCount)}}</span>
            </div>
            <div class="time" v-if="time">{{$utils.formatSecond(time/1000)}}</div>
            <miniIcon class="play-icon text-red" :size="44" type="zanting4"></miniIcon>
        </div>
        <div :class="this.flex?'w-3':''">
            <p class="fs-sm mb-2 text-dark-1 text-ellipsis">{{ name }}</p>
            <p class="fs-sm text-dark-1 text-ellipsis">{{ singer }}</p>
        </div>
    </div>
</template>

<script>

    export default {
        props: ['id', 'img', 'time', 'viewCount', 'name', 'singer','width','flex','padtop'],
        data: function () {
            return {}
        },
        methods: {
            onClickCard(){
                this.$router.push(`/mv/${this.id}`)
            }
        }
    }
</script>

<style scoped lang="scss">
    .w-3{
        width: 40%;
    }
.songCard{
    position: relative;
    width: calc(25% - 8px);
    margin: 4px;
    margin-bottom: 32px;
    cursor: pointer;
    .img-wrap{
        position: relative;
        width: 100%;
        padding-top: 60%;
        margin:0 8px 8px 0;
        border-radius: 4px;
        overflow: hidden;
        img{
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
        .viewCount{
            position: absolute;
            padding: 6px;
            right: 0;
            top: 0;
            color: white;
            font-size: 14px;

        }
        .time{
            position: absolute;
            font-size: 14px;
            padding: 6px;
            right: 0;
            bottom: 0;
            color: white;
        }
        .play-icon {
            opacity: 0;
            position: absolute;
            right: calc(50% - 22px);
            bottom: calc(50% - 22px);
            transition: all 0.3s;
            color: white;
        }

        &:hover {
            .play-icon {
                opacity: 1;
            }
        }
    }

}

</style>