<template>
    <div>
        <div class="d-flex text-dark">
            <div class="first"></div>
            <div v-for="(item,i) in headData" :key="i" class="second fs-md"
                 :class="i===0?'text-primary-1':''" >{{item}}</div>
            <div class="third fs-md">时长</div>
        </div>
        <div class="song d-flex text-dark ai-center pointer" v-for="(item,index) in songList"
             :key="index" @click="play(item, index)">
            <div class="first">
                <miniIcon class="number text-red-1" v-if="index===activeIndex"
                          :size=14 type="yinliang"></miniIcon>
                <span class="number" v-else>{{ index>9?index:'0'+index }}</span>
                <span v-if="hasImg">
                    <img v-lazy="$utils.getImgUrl(item.img,60)" alt="">
                    <miniIcon class="centerIcon text-red-1" type="zanting4"></miniIcon>
                </span>

            </div>
            <div v-for="(child,index1) in arr" :key="child" class="second fs-sm text-ellipsis"
                 :class="(index1===0?'text-primary-1':'')+' '+(index===activeIndex?'text-red-1':'')" >
                {{item[child]}}
            </div>
            <div class="third fs-md text-ellipsis">{{$utils.formatSecond(item.durationSecond)}}</div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
      import { eventBus } from '../utils/eventBus'
    import { playSong } from '../utils/mixin'
    export default {
        mixins: [playSong],
        props: {
            songList: {type: Array},
            hasImg: {type: Boolean, default: true}
        },
        mounted() {
            eventBus.$on("playAll",()=>{
                this.playSong(this.songList[0])
                this.activeIndex = 0
            })
        },
        data: function () {
            return {
                headData: ['音乐标题','歌手','专辑'],
                arr: ['name','artistsText','albumName'],
                isPlay: false,
                activeIndex: -1
            }
        },
        methods: {
            play(item,index){
                this.activeIndex = index
                this.playSong(item)
            }
        },

    }
</script>

<style scoped lang="scss">
    .first{
        width: 15%;
        padding: 5px;
        position: relative;
        .number{
            height: 60px;
            display: block;
            float: left;
            line-height: 60px;
            font-size: 12px;
            margin-right: 30px;
        }
        .centerIcon{
            position: absolute;
            left: 43%;
            top: 30%;
        }
    }
    .second{
        width: 25%;
        padding:5px 15px;
    }
    .third{
        width: 8%;
        padding: 5px;
    }
    .song:hover{
        background-color: #4a4a4a;
    }

</style>