import { getAlbum } from '../api/found'
export function getImgUrl(url, w, h) {
    if(!h){
        h = w
    }
    url += `?param=${w}y${h}`
    return url
}
export function formatSinger(arr) {
    return arr.map(item=> item.name).join('/')
}

export function scrollInto(dom){
    dom.scrollIntoView({behavior: "smooth"})
}

export function formatTime(time) {
    let t = new Date(time)
    let year = t.getFullYear()
    let month = (t.getMonth()+1)>9?(t.getMonth()+1):'0'+(t.getMonth()+1)
    let date = (t.getDate()+1)>9?(t.getDate()+1):'0'+(t.getDate()+1)
    return year+'-'+month+'-'+date
}

export function formatCount(count){
    if(count>100000){
        return Math.floor(count/10000)+'万'
    }else{
        return count
    }
}

export function createSong(song) {
    const { id, name, img, artists, duration, albumId, albumName,mvId, ...rest } = song
    return {
        id,
        name,
        img,
        artists,
        duration,
        albumName,
        url: genSongPlayUrl(song.id),
        artistsText: genArtistisText(artists),
        durationSecond: duration / 1000,
        // 专辑 如果需要额外请求封面的话必须加上
        albumId,
        // mv的id 如果有的话 会在songTable组件中加上mv链接。
        mvId,
        ...rest
    }
}

function genSongPlayUrl(id) {
    return `https://music.163.com/song/media/outer/url?id=${id}.mp3`
}

export function genArtistisText(artists) {
    return (artists || []).map(({ name }) => name).join('/')
}

export function formatSecond(time){
    let sec = Math.floor(time/60)
    let min = Math.floor(time%60)
    return (sec>9?sec:('0'+sec))+':'+ (min>9?min:('0'+min))
}

export async function getSongImg(id, albumId) {
    if (!(albumId !== undefined && albumId !== null)) {
        throw new Error('need albumId')
    }
    const { songs } = await getAlbum(albumId)
    const {
        al: { picUrl }
    } = songs.find(({ id: songId }) => songId === id) || {}
    return picUrl
}

export function shallowEqual(a, b, compareKey) {
    if (a.length !== b.length) {
        return false
    }
    for (let i = 0; i < a.length; i++) {
        let compareA = a[i]
        let compareB = b[i]
        if (compareKey) {
            compareA = compareA[compareKey]
            compareB = compareB[compareKey]
        }
        if (!Object.is(a[i], b[i])) {
            return false
        }
    }
    return true
}

