<template lang="html">
    <div class="d-flex jc-between bg-gray-2 d-footer ai-center">
        <div>
            <div v-if="hasCurrentSong" class="d-flex ml-4 ai-center">
                <img v-lazy="$utils.getImgUrl(currentSong.img,40)" alt="">
                <div class="text-gray-1 ml-2">
                    <div>
                        <span class="fs-md text-white">{{currentSong.name}}</span>
                        <span class="fs-xs"> - {{ $utils.genArtistisText(currentSong.artists) }}</span>
                    </div>
                    <div class="fs-xs">{{ $utils.formatSecond(currentTime) }} / {{ $utils.formatSecond(currentSong.durationSecond) }}</div>
                </div>
            </div>
        </div>
        <div class="text-red text-c">
            <mini-icon :size=32 class="pointer ml-4" type="shangyishou" @click="prev"></mini-icon>
            <mini-icon :size=36 class="pointer ml-4" v-if="hasCurrentSong&&isPlay" type="zanting" @click="pause"></mini-icon>
            <mini-icon :size=36 class="pointer ml-4" v-else type="zanting11" @click="play()"></mini-icon>
            <mini-icon :size=32 class="pointer ml-4" type="xiayishou" @click="next"></mini-icon>
        </div>
        <div class="text-dark-1 mr-4 d-flex ai-center">
            <mini-icon class="pointer ml-4" type="fenxiang"  @click="dialogVisible = true"></mini-icon>
            <el-tooltip class="item" effect="dark" :content="playSeq[playMode].text" placement="top">
                <mini-icon class="pointer ml-4" :type="playSeq[playMode].icon" @click="changeMode"></mini-icon>
            </el-tooltip>
            <el-popover
                    :value="isPlayListShow"
                    placement="top"
                    trigger="manual"
                    width="400"
                    visible-arrow=false
            >
                <div style="text-align: center;">
                    <span class="mr-3 pointer" :class="active?'text-red-1':''" @click="tabClick">播放列表</span>
                    <span class="pointer" :class="active?'':'text-red-1'" @click="tabClick">历史记录</span>
                </div>
                <SongPrompt v-if="active"></SongPrompt>
                <v-history v-else></v-history>
                <mini-icon class="pointer ml-4" slot="reference" type="yinleliebiao" @click="togglePlayListShow"></mini-icon>
            </el-popover>

            <mini-icon class="pointer ml-4" v-if="volActive" type="yinliang" @click="changeVolState"></mini-icon>
            <mini-icon class="pointer ml-4" v-else type="yinliangguanbi" @click="changeVolState"></mini-icon>
            <Progress class="ml-2" @changeVolume="changeVol" width="100px" :disabled="volActive"></Progress>
        </div>
        <div class="progress">
            <Progress width="100%" :disabled="songReady" :percent="playedPercent" @changeProgress="updateProgress"></Progress>
        </div>
        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%"
                customClass="bg-dark"
                :modal = false
                :before-close="handleClose">
            <span>分享链接已经复制到剪贴板，快去和小伙伴一起听歌吧！</span>
            <div class="dialog-btn" @click="dialogVisible=false">确定</div>
        </el-dialog>

        <audio :src="currentSong.url" @canplay="ready" @ended="end"
               @timeupdate="updateTime" ref="audio"></audio>
    </div>
</template>

<script type="text/ecmascript-6">
    import { mapState, mapMutations, mapGetters, mapActions } from '../store/helper/music'
    import SongPrompt from '../base/SongPromptList.vue'
    import VHistory from '../base/History.vue'
    import Progress from '../base/Volume.vue'
    import { VOLUME_KEY } from '../utils/config'
    import storage from "good-storage"
    export default {
        data: function () {
            return {
                active: true,
                promptUrl: '',
                isPlayErrorPromptShow: false,
                songReady: false,
                isPlay: false,
                dialogVisible: false,
                volume: storage.get(VOLUME_KEY, 0.5),
                volActive: true,
                playSeq: [{text: '顺序播放',icon: '_shunxubofang'},
                         {text: '随机播放',icon: 'suijibofang'},
                         {text: '单曲播放', icon: 'danqubofang'}]
            }
        },
        mounted(){
          this.audio.volume = this.volume
        },
        methods: {
            handleClose(done) {
                this.dialogVisible = false
            },
            ready(){
                this.songReady = true
            },
            end(){
                this.setCurrentTime(0)
                this.startSong(this.endNextSong)
            },
            async play(){
                this.isPlay = true
                if(this.songReady){
                    try{
                        await this.audio.play()
                        if(this.isPlayErrorPromptShow){
                            this.isPlayListPromptShow = false
                        }
                    }catch(error){
                        // 提示用户手动播放
                        this.isPlayErrorPromptShow = true
                        this.setPlayingState(false)
                    }
                }
            },
            pause(){
                this.audio.pause()
                this.isPlay = false
            },
            prev(){
                this.startSong(this.prevSong)
            },
            next(){
              this.startSong(this.nextSong)
            },
            updateTime(e){
                const time = e.target.currentTime
                this.setCurrentTime(time)
            },
            updateProgress(percent) {
                this.audio.currentTime = this.currentSong.durationSecond * percent
                this.setPlayingState(true)
            },
            togglePlayListShow(){
              this.setPlayListShow(!this.isPlayListShow)
            },
            tabClick(){
              this.active = !this.active
            },
            changeMode(){
                let mode = this.playMode
                if(mode===2){
                    mode=0
                }else{
                    mode++;
                }
              this.setPlayMode(mode)
            },
            changeVol(w){
                storage.set(VOLUME_KEY,w/100)
                this.volume = storage.get(VOLUME_KEY)
                this.audio.volume = this.volume
            },
            changeVolState(){
                this.volActive = !this.volActive
            },
                ...mapMutations([
                    "setCurrentTime",
                        "setPlayingState",
                        "setPlayMode",
                        "setPlayListShow",
                        "setPlayerShow"
                ]),
                ...mapActions(["startSong"])
        },
        watch: {
            currentSong(newSong, oldSong){
              // 清空歌曲
                if(!newSong.id){
                    this.audio.pause()
                    this.audio.currentTime = 0
                    return
                }
                // 单曲循环

                //
                this.songReady = false
                if(this.timer){
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(() => {
                    this.play()
                }, 1000)
            }
        },
        computed: {
            audio(){
                return this.$refs.audio
            },
            hasCurrentSong(){
              return (this.currentSong.id !== undefined) && (this.currentSong.id !== null)
            },
            // 播放的进度百分比
            playedPercent() {
                const { durationSecond } = this.currentSong
                return Math.min(this.currentTime / durationSecond, 1) || 0
            },
          ...mapState([
              "currentSong",
              "currentTime",
                  "playing",
                  "playMode",
                  "isPlayListShow",
                  "isPlayListPromptShow",
                  "isPlayerShow"
          ]),
            ...mapGetters(["prevSong", "nextSong", "endNextSong"])
        },
        components: {
            SongPrompt,VHistory,Progress
        }
    }
</script>

<style lang="scss">
    .progress{
        position: absolute;
        top: -10px;
        width: 100%;
    }
    .d-footer{
        min-height: 8%;
        position: fixed;
        width: 100%;
        border-top: 2px solid #212121;
        box-shadow: 0 -1px 3px 1px #212121;
    }
    .d-mask{
        width: 40px;
        height: 40px;
        border-radius: 4px;
    }
    .dialog-btn{
        width: 100%;
        height: 26px;
        line-height: 26px;
        text-align: center;
        background-color: #f44336;
        margin-top: 20px;
        color: white;
    }
    .el-popover{
        height: 80% !important;
        overflow: scroll;
        background: #2c2d2a !important;
        border: none !important;
        /*transform: translateY(-50px)*/
    }
    .el-popper[x-placement^=top] .popper__arrow::after{
        border-top-color: #2c2d2a !important;
    }

</style>