<template>
    <div>
        <div class="mt-3 ml-3">
            <strong class="title">{{ this.keyword }}</strong>
            <span class="text-dark fs-md">找到{{songCount}}个结果</span>
        </div>
        <div class="p-5">
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="歌曲" name="song">
                    <SongList :songList="songs" :hasImg=false></SongList>
                </el-tab-pane>
                <el-tab-pane label="歌单" name="playlist">
                    <router-view></router-view>
                </el-tab-pane>
                <el-tab-pane label="MV" name="mv">
                    <router-view></router-view>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import SongList from '../../base/SongList.vue'
    import { getSearch } from '../../api/search'
    export default {
        props: ['keyword'],
        data: function () {
            return {
                songCount: 0,
                songs: [],
                activeName: 'song'
            }
        },
        async created(){
            this.getData()

        },
        methods: {
            async getData(){
                const {result:{songCount, songs}} = await getSearch({'keywords': this.keyword})
                this.songCount = songCount
                this.normalizeSearch(songs)
                console.log(this.songs)
            },
            normalizeSearch(songs){
                this.songs = songs.map(song => {
                    const {
                            id, mvid, name, alias, artists, duration, album
                    } = song
                    return this.$utils.createSong({
                        id,
                        name,
                        alias,
                        artists,
                        duration,
                        mvId: mvid,
                        albumName: album.name,
                        albumId: album.id
                    })
                })
            },
            handleClick(){

            }
        },
        computed: {

        },
        components: {
            SongList
        }
    }
</script>

<style lang="scss">
.title{
    font-size: 32px;
    color: #b1b1b1;
    font-weight: 700;
    margin-right: 24px;
}

</style>