
<script>
    export default {
        props: {
          color: {
              type: String,
              default: "#f44336"
          },
            height: {
                type: Number,
                default: 8
            }
        },
        data: function () {
            return {}
        },
        render(h){
            return h('div',{class: ['text-white','ai-center','pl-4','font','d-flex'],
                style: {backgroundColor: this.color, height: this.height+'%'}
        },this.$slots.default)
        }
    }
</script>

<style scoped>
.font{
    font-weight: 500;
    font-family: Roboto,sans-serif!important;
    letter-spacing: 3px;
    width: 100%;
    position: fixed;
}

</style>