import storage from 'good-storage'

export default{
    // 侧边栏控制
    isSideBarShow: true,
    // 当前播放歌曲
    currentSong: {},
    // 当前播放时长
    currentTime: 0,
    // 播放状态
    playing: false,
    // 播放模式 0代表顺序，1代表随机，2代表
    playMode: 0,
    // 播放列表显示
    isPlayListShow: false,
    // 播放提示显示
    isPlayListPromptShow: false,
    // 播放列表数据
    playList: [],
    // 歌曲详情页显示
    isPlayerShow: false,
    // 播放历史数据
    playHistory: storage.get('historyList',[])
}