import { mapActions, mapMutations, mapState } from '@/store/helper/music'
import {  getSongDetail } from "../api/suggestList"
export const playSong = {
    methods: {
        nomalizeSong(song) {
            const {
                id,
                name,
                song: {
                    mvId,
                    artists,
                    album: { blurPicUrl },
                    duration
                }
            } = song
            return this.$utils.createSong({
                id,
                name,
                img: blurPicUrl,
                artists,
                duration,
                mvId: mvId
            })
        },
        playSong(item){
            // const nomalizedSong = this.nomalizeSong(item)
            // const normalizedSongs = this.normalizedSongs
            this.startSong(item)
            this.setPlayList(this.songList)
        },
        ...mapMutations(["setPlayList"]),
        ...mapActions(["startSong"]),
    },
    watch:{
        currentSong(newSong, oldSong){
            if(this.songList){
                this.activeIndex = this.songList.findIndex((item,index) =>{
                    return item.id===newSong.id
                })
            }
            immediate: true
        }
    },
    computed: {
        normalizedSongs(){
            return this.songList.map(song => this.nomalizeSong(song))
        },
        ...mapState(["currentSong"])
    },
}

export const getSongList = {
    methods: {
        async getSongList(playList){
            const trackIds = playList.trackIds.map(({id})=> id)
            const songDetails = await getSongDetail(trackIds.slice(0,MAX))
            const songs = songDetails.songs.map(({id, name, al, ar, mv, dt}) =>
                this.$utils.createSong({id,name,artists: ar, duration: dt, mvid: mv, albumName: al.name, img: al.picUrl}))
            this.songs = songs
            console.log(this.songs)
        }
    }
}