<template lang="html">
    <div class="d-flex p-4">
        <div class="flex1 pl-6">
            <div>
                <h3 class="text-dark">MV详情</h3>
                <div class="my-4 pr-4">
                    <videoPlayer :url="mvPlayInfo.url" ref="video"></videoPlayer>
                </div>
                <div class="star my-3 d-flex ai-center text-dark">
                    <img v-lazy="$utils.getImgUrl(artist.picUrl,60)"
                         class="round mr-4" alt="">
                    <p>{{ artist.name }}</p>
                </div>
                <h1 class="fs-xxl mt-4 text-dark">{{ mvDetail.name}}</h1>
                <div class="fs-sm my-3 text-gray-1">
                    发布：<span>{{$utils.formatTime(mvDetail.publishTime)}}</span>
                    播发：{{ $utils.formatCount(mvDetail.playCount) }}次
                </div>
            </div>
            <div>
                <Comment :id="this.id" type="mv"></Comment>
            </div>
        </div>
        <div class="flex2">
            <h3 class="text-dark">相关推荐</h3>
            <MvCard v-for="mv in simiMvs" :key="mv.id"
            :id="mv.id" :img="mv.cover" :time="mv.duration" width="60"
                    :name="mv.name" :viewCount="mv.playCount" flex="true"
            :singer="mv.artistName" padtop="35%"></MvCard>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import videoPlayer from '../base/VideoPlayer.vue'
    import MvCard from '../base/MvCard.vue'
    import Comment from '../base/Comment.vue'
    import { mapState, mapMutations } from '../store/helper/music'
    import { getMvDetail, getMvUrl, getArtists, getSimiMv } from '../api/mv'

    export default {
        props: {
          id: {type: Number, required: true}
        },
        data: function () {
            return {
                mvDetail: {},
                mvPlayInfo: "",
                artist: {},
                simiMvs: []
            }
        },
        methods: {
            async init(){
                const [{data: mvDetail},{data: mvPlayInfo},{mvs: simiMvs}] =
                        await Promise.all([getMvDetail(this.id),getMvUrl(this.id),getSimiMv(this.id)]);
                const { artist } = await getArtists(mvDetail.artistId)
                this.artist = artist
                console.log(simiMvs)
                this.mvDetail = mvDetail
                this.mvPlayInfo = mvPlayInfo
                this.simiMvs = simiMvs
            },
                ...mapMutations(['setIsSideBarShow'])

        },
        created(){
            this.setIsSideBarShow(false)
            this.init()
        },
        components: { videoPlayer,MvCard,Comment }
    }
</script>

<style scoped lang="scss">
.flex1{
    flex-grow: 2;
    max-width: 66%;
    .star{
        height: 60px;
        line-height: 60px;
    }
}

    .flex2{
        flex-grow: 1
    }
</style>