<template>
    <div>
        <div class="mb-6" v-if="shouldHotCommentShow">
            <h2 class="text-dark">精彩评论</h2>
            <Comm :hotComm="hotComm"></Comm>
        </div>
        <div v-if="shouldCommentShow">
            <h2 ref="commentTitle" class="text-dark">最新评论 ({{ total }})</h2>
            <Comm :hotComm="comm"></Comm>
        </div>
        <div class="text-right" >
            <el-pagination
                    background
                    :current-page.sync="currentPage"
                    :page-size="pageSize"
                    @current-change="onPageChange"
                    layout="prev, pager, next"
                    :total="total">
            </el-pagination>
        </div>
        <div v-if="!shouldCommentShow && !shouldHotCommentShow" class="text-dark">
            还没有评论哦~
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import { getSongComment, getPlaylistComment, getHotComment, getMvComment } from '../api/comment'
    import Comm from './Comm.vue'
    const SONG_TYPE = "song"
    const PLAYlIST_TYPE = "playList"
    const MV_TYPE = "mv"
    const PAGE_SIZE = 20

    export default {
        props: {
            id: {type: Number},
            type: {type: String}
        },
        data: function () {
            return {
                loading: false,
                total: 0,
                currentPage: 1,
                pageSize: 0,
                hotComm: [],
                comm: []
            }
        },
        created(){
            this.pageSize = PAGE_SIZE
            this.init()
        },
        methods: {
            async init(){/*
                if(!this.id) return
                switch(this.type){
                    case 'music':
                        const res = getSongComment(this.id)
                            console.log(res)
                        break;
                    case 'mv':
                        const res =
                }*/
                this.loading = true
                const commentRequestMap = {
                    [PLAYlIST_TYPE]: getPlaylistComment,
                    [SONG_TYPE]: getSongComment,
                    [MV_TYPE]: getMvComment
                }

                const commentRequest = commentRequestMap[this.type]
                const { hotComments = [], comments = [], total} = await
                        commentRequest({
                            id: this.id,
                            pageSize: PAGE_SIZE,
                            offset: this.getPageOffset(this.currentPage, PAGE_SIZE)
                        }).finally(() => {
                            this.loading = false
                        })
                //歌单热评需要单独请求接口获取
                if(this.type === PLAYlIST_TYPE && this.currentPage === 1){
                    const { hotComments: exactHotComments = []} = await getHotComment({
                        id: this.id,
                        type: 2
                    })
                    this.hotComm = exactHotComments
                }else{
                    this.hotComm = hotComments
                }

                this.comm = comments
                this.total = total
            },
            getPageOffset(page, limit) {
                return (page - 1) * limit
            },
            async onPageChange(){
                await this.init()
                this.$nextTick(() => {
                    this.$utils.scrollInto(this.$refs.commentTitle)
                })

            }
        },
        computed: {
            shouldHotCommentShow() {
                return this.hotComm.length > 0 && this.currentPage === 1
            },
            shouldCommentShow() {
                return this.comm.length > 0
            }
        },
        watch: {
            id: {
                handler(newId){
                    if(newId){
                        this.currentPage = 1
                        this.init()
                    }
                },
                immediate: true
            }
        },
        components: {
            Comm
        }
    }
</script>

<style>
.c-user{
    float: left;
}
.el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background: #252525 !important;
}
.el-pagination.is-background .el-pager li:not(.disabled).active{
    background: #252525 !important;
    color: #f44336 !important;
}
    .number:hover{
        color: white !important;
    }
    .el-icon:hover{
        color: white !important;
    }
</style>