<template lang="html">
<div class="d-flex py-4">
    <img v-lazy="$utils.getImgUrl(img, 200)" alt="">
    <div class="text-dark ml-4">
        <p class="mb-2 text-primary-1">{{title}}</p>
        <div class="fs-md">
            <img class="avatar mr-3" v-lazy="$utils.getImgUrl(avatar,40)" alt="">
            <span class="mr-2">{{ createdMan}}</span>
            <span>{{ $utils.formatTime(time) }} 创建</span>
        </div>
        <div class="aBtn bg-red-1 pointer mt-3 text-white" @click="playSong">
            <miniIcon :size=10 type="zanting4"></miniIcon>
            <span class="ml-2">播放全部</span>
        </div>
        <p class="fs-md mb-2 mt-4">标签： {{tag.join('/')}}</p>
        <p class="fs-md">简介： {{desc}}</p>

    </div>
</div>
</template>

<script type="text/ecmascript-6">
    import { eventBus } from '../../utils/eventBus'
    export default {
        props: ['img', 'title', 'avatar', 'createdMan', 'time', 'tag', 'desc','song'],
        data: function () {
            return {

            }
        },
        methods: {
            playSong(){
                eventBus.$emit("playAll");
            }
        }
    }
</script>

<style scoped>
.avatar{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
    .aBtn{
        width: 100px;
        height: 30px;
        font-size: 14px;
        line-height: 30px;
        text-align: center;
        border-radius: 4px;
    }
</style>