<template>
    <div>
        <div class="d-flex jc-between mt-4 b-line">
            <span>总共{{playList.length}}首</span>
            <span v-if="playList.length!==0" class="pointer" @click="removePlayList">
                <miniIcon type="shanchu-copy" :size="18"></miniIcon>
                清空
            </span>
        </div>
        <div v-if="playList.length!==0">
            <div class="d-flex py-2">
                <div class="f-percent text-dark">音乐标题</div>
                <div class="f-percent">歌手</div>
                <div class="tw-percent">时长</div>
            </div>
            <div class="d-flex p-1" v-for="(song,i) in playList" :key="i" @click="play(song)">
                <div class="f-percent text-dark text-ellipsis">
                    <span :class="currentSong.id===song.id?'text-red-1':''">{{song.name}}</span>
                    <miniIcon v-if="song.mvId!==0" class="text-red-1 pointer ml-1" type="shipin"
                              @click="toMv($event)" :size=16></miniIcon>
                </div>
                <div class="f-percent text-ellipsis">{{song.artistsText}}</div>
                <div class="tw-percent">{{$utils.formatSecond(song.durationSecond)}}</div>
            </div>
        </div>
        <div v-else class="empty">
            你还没有添加任何歌曲
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import { mapState,mapMutations, mapActions } from "../store/helper/music"
    export default {
        props: {
//            songList: {type: Array}
        },
        data: function () {
            return {

            }
        },
        computed: {
                ...mapState([
                     "playList",
                        "currentSong"
                ])
        },
        methods: {
            toMv(e){
                console.log('vm')
                e.stopPropagation()
            },
            play(song){
                this.startSong(song)
            },
            removePlayList(){
                this.clearPlayList()
            },
            ...mapActions(["startSong","clearPlayList"])
        },


    }
</script>

<style scoped lang="scss">
.f-percent{
    width: 44%;
}
    .tw-percent{
        width: 12%
    }
    .b-line{
        border-bottom: 1px solid #b1b1b1;
    }
    .empty{
        transform: translateY(100%);
        text-align: center;

    }
</style>