<template>
    <div class="box">
        <el-popover
                :value="active"
                placement="bottom"
                trigger="focus"
                width="400"
                visible-arrow=false
        >
            <div slot="reference">
                <input type="text" @input="onInput(searchName)" class="v-input" :placeholder="value"
                       :style="wh" @focus="focusInput" v-model="searchName">
                <div class="box1"></div>
                <mini-icon type="icon-search" class="l-search" @click="search(searchName)" ></mini-icon>
            </div>
            <div v-if="suggestShow">
                <div v-for="(normalizedSuggest,index) in normalizedSuggests" :key="index">
                    <div class="text-dark mb-3">
                        <miniIcon :size="14" :type="normalizedSuggest.icon"></miniIcon>
                        {{normalizedSuggest.title}}</div>
                    <div @click="normalizedSuggest.onClick(item)"
                         v-for="item in normalizedSuggest.data" :key="item.id" class="pl-3 py-1 text-dark fs-sm pointer item-1">
                        <p>{{normalizedSuggest.renderName?normalizedSuggest.renderName(item):item.name}}</p>
                    </div>
                </div>
            </div>
            <div v-else class="p-3">
                <div>
                    <p class="mb-3">热门搜索</p>
                    <div class="d-flex flex-wrap">
                        <p class="search-item" @click="search(item.first)"
                           v-for="(item,index) in hotSearch" :key="index">{{item.first}}</p>
                    </div>
                </div>
                <div class="mt-5">
                    <p>搜索记录</p>
                    <div></div>
                </div>
            </div>

        </el-popover>
    </div>
</template>

<script type="text/ecmascript-6">
    import { getSearchHot, getSearchSuggest } from '../api/search'
    import storage from 'good-storage'
    import { mapActions, mapMutations } from "../store/helper/music"
    import { debounce, throttle } from 'lodash-es'
    import {formatSinger} from '../utils/common'

    export default {
        props: {
          width: {type: Number, default: 400},
            height: {type: Number, default: 32},
            value: {type: String, default: 'Search...'},
            color: {type: String, default: '#f99f98'}
        },
        data: function () {
            return {
                wh:{width: this.width+'px',height: this.height+'px', color: this.color},
                underStyle:{width: this.width+'px',height: this.width+'px', backgroundColor: this.color},
                active: false,
                isSearch: false,
                searchName: '',
                hotSearch: [],
                searchHistorys: storage.get('search',[]),
                suggest: {}
            }
        },
        async created(){
            const { result: {hots} } = await getSearchHot()
            this.hotSearch = hots
        },
        methods: {
            focusInput(){
              this.active = !this.active
            },
            async search(name){
                this.searchName = name
                this.searchHistorys.push({first: name})
                storage.set('search',this.searchHistorys)
                this.$router.push(`/search/${name}`)
                this.active = false
            },
            onInput: debounce(function (val) {
                if(!this.searchName.trim()){
                    return
                }
                getSearchSuggest(this.searchName).then(({ result }) => {
                    this.suggest = result
                })
            }, 500),
            async onClickSong(item) {
                const {
                        id,
                        name,
                        artists,
                        duration,
                        mvid,
                        album: { id: albumId, name: albumName }
                } = item
                const song = this.$utils.createSong({
                    id,
                    name,
                    artists,
                    duration,
                    albumId,
                    albumName,
                    mvId: mvid
                })
                this.startSong(song)
                this.addToPlaylist(song)
            },
            onClickPlaylist(item){
                const {id} = item
                this.$router.push(`/home/suggestList/${id}`)
                this.active = false
            },
            onClickMv(mv){
                const {id} = mv
                this.$router.push(`/mv/${id}`)
                this.active = false
            },
            ...mapMutations(['setPlayList']),
            ...mapActions(['startSong','addToPlaylist'])
        },
        computed: {
            suggestShow(){
                return (this.searchName.length && ["songs","playlists"].find((key) => {
                    return this.suggest[key] && this.suggest[key].length})
                )
            },
            normalizedSuggests(){
                return [
                    {
                        title: "单曲",
                        icon: "yinle1",
                        data: this.suggest.songs,
                        renderName(song) {
                            return `${song.name} - ${formatSinger(song.artists)}`
                        },
                        onClick: this.onClickSong.bind(this)
                    },
                    {
                        title: "歌单",
                        icon: "yinleliebiao",
                        data: this.suggest.playlists,
                        onClick: this.onClickPlaylist.bind(this)
                    },
                    {
                        title: "mv",
                        icon: "shipin",
                        data: this.suggest.mvs,
                        renderName(mv) {
                            return `${mv.name} - ${genArtistisText(mv.artists)}`
                        },
                        onClick: this.onClickMv.bind(this)
                    }
                ].filter(item => item.data && item.data.length)
            }
        }
    }
</script>

<style scoped lang="scss">
    .box{
        position: relative;

    .v-input{
        border: none;
        font-size: 16px;
        font-weight: 400;
        outline: none;
        letter-spacing: 2px;
        background: transparent;
        border-bottom: 1px solid ;
    &:hover{
         border-bottom: 1px solid #fff;
     }
    &:focus{
         color: #fff !important;
     }
    }
    .box1{
        width: 1px;
        height: 2px;
        display: block;
        background-color: #fff;
        transition: .5s ease-out;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: scaleX(0);
    }
    .l-search{
        position: absolute;
        right: 0;
        cursor: pointer;
        bottom: 2px;
    }
    }

    .search-item{
        padding: 4px 12px;
        margin: 5px;
        border: 1px solid #5C5C5C;
        color: #d8d6d3;
        font-size: 12px;
        cursor: pointer;
        &:hover{
            background-color: #4a4a4a;
         }
    }
    .item-1{
        &:hover{
            background: #252525;
         }
    }

</style>