<template lang="html">
    <div class="newSongCard pl-3 py-3 d-flex ai-center text-dark-1 fs-sm" @click="onClickCard">
        <div class="mr-3">{{ num|numFormat }}</div>
        <div class="p-rel">
            <img v-lazy="$utils.getImgUrl(img, 60)" alt="">
                <miniIcon class="newIcon text-red-1" type="zanting4"></miniIcon>
        </div>
        <div class="mx-3">
            <p>{{ name }}</p>
            <p class="text-dark mt-3">{{ singer }}</p>
        </div>
    </div>
</template>

<script>

    export default {
        props: ['id', 'num', 'img', 'name', 'singer'],
        filters: {
            numFormat(value){
                return value>=10?value:'0'+value
            }
        },
        data: function () {
            return {}
        },
        methods: {
            onClickCard(){
//                this.$router.push()
            }
        }
    }
</script>

<style scoped lang="scss">
.newSongCard{
    width: calc(50% - 10px);
    .newIcon{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-12px, -12px)
    }
    &:hover{
        background-color: #2f2f2f;

     }
}

</style>