<template>
    <div style="height: 84%" class="d-flex">
        <div class="sideBar bg-gray pt-2" ref="sideBar">
            <div class="sideItem first pointer text-dark-1" @click="login">
                <mini-icon class="ml-4" type="yonghu"></mini-icon>
                <span class="px-5 fs-lg">未登录</span>
            </div>
            <div  v-for="(item,i) in items" :keys="item.icon"  @click="handleActive(i)">
                <router-link :to="item.to"
                             tag="div" class="sideItem pointer text-primary-1"
                             :class="active===i?'active':''">
                    <mini-icon class="ml-4" :size=18 :type="item.icon"></mini-icon>
                    <span class="px-5 fs-md">{{item.text}}</span>
                </router-link>
            </div>
        </div>
        <div class="container bg-primary flex-1">
            <router-view></router-view>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import { mapState } from '../store/helper/music'

    export default {
        data: function () {
            return {
                items: [
                    {to: '/home', text: '发现音乐', icon: 'vynil'},
                    {to: '/suggest', text: '推荐歌单', icon: 'tuijian'},
                    {to: '/newMusic', text: '最新音乐', icon: 'yinle1'},
                    {to: '/newMV', text: '最新MV', icon: 'shipin'},
                ],
                active: 0
            }
        },
        methods: {
            handleActive(i){
                this.active = i;
            },
            login(){

            },
        },
        watch: {
          isSideBarShow(){
            if(!this.isSideBarShow){
                this.$refs.sideBar.style.display = 'none';
            }else{
                this.$refs.sideBar.style.display = 'block';
            }
          }
        },
        computed: {
            ...mapState(['isSideBarShow'])
        }
    }
</script>

<style lang="scss" scoped>
    .sideItem{
        width: 100%;
        height: 48px;
        font-weight: 500;
        letter-spacing: 1px;
        line-height: 48px;
        &.first{
            height:55px;
         }
    }
    .sideItem:hover{
        background-color: #5C5C5C;
    }
    .active{
        color: #ed655a;
        background-color: #2c2d2a;
    }
    .sideBar{
        min-width: 240px;
        height: 100%;
        transition: 0.5s ease-in-out;
    }
    .container{
        overflow: scroll;
        min-width: 1125px;
    }



</style>