<template lang="html">
<div class="px-6 py-5">
    <el-carousel :interval="4000" type="card" height="200px">
        <el-carousel-item v-for="(item,index) in banners" :key="index">
            <img v-lazy="$utils.getImgUrl(item.imageUrl,1000,400)" class="car-img">
        </el-carousel-item>
    </el-carousel>
    <!--end of Carousel-->
    <div class="my-5">
        <h3 class="mb-3 text-dark-1">推荐歌单</h3>
        <div class="d-flex flex-wrap">
            <SongCard v-for="item in personalList" :key="item.id"
                      :id="item.id" :img="item.picUrl" :name="item.name"
                      :desc="item.copywriter"></SongCard>
        </div>
    </div>
    <!--end of 推荐歌单-->
    <div class="my-5">
        <h3 class="mb-3 text-dark-1">最新音乐</h3>
        <div class="d-flex flex-wrap">
            <NewSongCard v-for="(item,index) in newSongs" :key="item.id"
                         :singer="$utils.formatSinger(item.song.artists)"
                         :num="index" :id="item.id" :img="item.picUrl"
                         :name="item.name"  @click.native="playSong(item,index)"></NewSongCard>
        </div>
    </div>
    <!--end of 推荐音乐-->
    <div>
        <h3 class="mb-3 text-dark-1">推荐MV</h3>
        <div class="d-flex flex-wrap">
            <MvCard v-for="item in mvs" :key="item.id"
                    :id="item.id" :img="item.picUrl" :name="item.name" :viewCount="item.playCount"
                    :singer="item.artistName"></MvCard>
        </div>
    </div>
    <!--end of 推荐Mv-->
</div>
</template>

<script type="text/ecmascript-6">
    import {getBanner,getPersonalized,getNewSongs,getPersonalizedMv} from "../api/found"
    import SongCard from '../base/SongCard.vue'
    import NewSongCard from '../base/NewSongCard.vue'
    import MvCard from '../base/MvCard.vue'
    import { mapActions, mapMutations } from '@/store/helper/music'

    export default {
        data: function () {
            return {
                banners: [],
                personalList: [],
                newSongs: [],
                mvs: []
            }
        },
        created(){
            this.getBanners()
            this.getPersonalizeds()
            this.getNewSongs()
            this.getPersonalizedMvs()
        },
        methods: {
            async getBanners(){
                const { banners } = await getBanner()
                this.banners = banners
            },
            async getPersonalizeds(){
                const { result } = await getPersonalized({limit: 10})
                this.personalList = result
            },
            async getNewSongs(){
                const { result } = await getNewSongs()
                this.newSongs = result
            },
            async getPersonalizedMvs(){
                const { result } = await getPersonalizedMv()
                this.mvs = result
            },
            nomalizeSong(song) {
                const {
                        id,
                        name,
                        song: {
                                mvid,
                                artists,
                                album: { blurPicUrl },
                                duration
                        }
                } = song
                return this.$utils.createSong({
                    id,
                    name,
                    img: blurPicUrl,
                    artists,
                    duration,
                    mvId: mvid
                })
            },
            playSong(item){
                const nomalizedSong = this.nomalizeSong(item)
                const normalizedSongs = this.normalizedSongs
                this.startSong(nomalizedSong)
                this.setPlayList(normalizedSongs)
            },
                ...mapMutations(["setPlayList"]),
                ...mapActions(["startSong"]),
        },
        computed: {
            normalizedSongs(){
                return this.newSongs.map(song => this.nomalizeSong(song))
            }
        },
        components: {
            SongCard,NewSongCard,MvCard
        }
    }
</script>

<style scoped>

    .car-img{
        width: 100%;
        height: 100%;
        border-radius: 4px;
    }
</style>